.section-title {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  text-align: center;
  letter-spacing: -1.5px;

  @include onTablet {
    font-size: 48px;
  }

  @include onDesktop {
    margin-bottom: 56px;
    font-size: 64px;
  }

  &--align--left {
    text-align: left;
  }

  &--about-us {
    font-weight: 500;
    font-size: 23px;
    line-height: 32.2px;
  }
}
